.card {
  background-color: cornsilk;
  color: #3D7DCA;
  border: 5px solid #003A70;
  max-width: 35vw;
  min-width: 35vw;
  max-height: 100%;
  min-height: 100%;
  margin-top: 1vw;
  margin-bottom: 1vw;
  margin-left: 1vw;
  margin-right: 1vw;
  align-items: center;
  text-align: center;
}

.card * {
  text-overflow: ellipsis;
  font-size: larger;
  text-decoration: none;
}

.pokemon-details {
  background-color: #FFCB05;
  border: 2px dotted #003A70;
  color: #3D7DCA;
}

.ability {
  border: black solid 1px;
  background-color: whitesmoke;
}

